const studentModel = require('../models/student.model');
const {createStudentValidation,loginValidation} = require('../validations/auth.validation');

const signup = (req,res,next) => {
    const  {error } = createStudentValidation(req.body);
    if (error) {
        res.status(400).send({message:error.details['0'].message})
    } else {
        try {
            const {firstName, lastName,email,Phone,address,meeting_time} = req.body;
            const student = new studentModel({firstName, lastName,email,Phone,address,meeting_time});
            res.send({message:"sign up api"})
        } catch (error) {
            res.status(400).send({message:error.details['0'].message})
        }
    }
}

const login = (req,res,next) => {
    
    const {error} = loginValidation(req.body);
    if (error) {
        res.status(error.status).send({message:error.details['0'].message});
    } else {
        try {
            const {username,password} = req.body;
            const student = new studentModel({username,password});
            res.send({data:student})
        } catch (error) {
            res.status(error.status).send({message:error.details['0'].message});
        }
    }
}

module.exports = { signup, login }